/* Standardized glow effects */
.glow-orange-subtle {
  box-shadow: 0 4px 15px rgba(229, 95, 42, 0.3);
}

.glow-orange-medium {
  box-shadow: 0 8px 25px rgba(229, 95, 42, 0.4);
}

.glow-orange-strong {
  box-shadow: 0 12px 35px rgba(229, 95, 42, 0.5);
}

/* Simplified glow animation */
@keyframes glowPulse {
  0%, 100% { 
    box-shadow: 0 0 20px rgba(229, 95, 42, 0.4);
  }
  50% { 
    box-shadow: 0 0 30px rgba(229, 95, 42, 0.6);
  }
}

.glow-pulse {
  animation: glowPulse 2s ease-in-out infinite;
}

/* Typing indicator animation */
@keyframes typing {
  0%, 60%, 100% {
    transform: translateY(0);
    opacity: 0.7;
  }
  30% {
    transform: translateY(-10px);
    opacity: 1;
  }
}

.typing-dot {
  animation: typing 1.4s infinite;
}

.typing-dot:nth-child(2) {
  animation-delay: 0.2s;
}

.typing-dot:nth-child(3) {
  animation-delay: 0.4s;
}

/* Enhanced Custom scrollbar with smooth behavior */
.custom-scrollbar {
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  will-change: scroll-position;
}

.custom-scrollbar::-webkit-scrollbar {
  width: 8px;
}

.custom-scrollbar::-webkit-scrollbar-track{
  border-radius: 9999px;
  --tw-bg-opacity: 1;
  background-color: rgb(31 41 55 / var(--tw-bg-opacity, 1));
}

.custom-scrollbar::-webkit-scrollbar-thumb{
  border-radius: 9999px;
  --tw-bg-opacity: 1;
  background-color: rgb(245 88 24 / var(--tw-bg-opacity, 1));
  -webkit-transition: background-color 0.2s ease;
  transition: background-color 0.2s ease;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(255 112 51 / var(--tw-bg-opacity, 1));
}

/* Chat-specific scrollbar with enhanced smoothness */
.chat-scrollbar {
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  will-change: scroll-position;
  transform: translateZ(0);
  scroll-padding-bottom: 160px;
}

.chat-scrollbar::-webkit-scrollbar {
  width: 6px;
}

.chat-scrollbar::-webkit-scrollbar-track{
  border-radius: 9999px;
  background-color: rgb(31 41 55 / 0.5);
}

.chat-scrollbar::-webkit-scrollbar-thumb{
  border-radius: 9999px;
  background-color: rgb(245 88 24 / 0.8);
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.chat-scrollbar::-webkit-scrollbar-thumb:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(255 112 51 / var(--tw-bg-opacity, 1));
}

/* Connection status indicator */
@keyframes pulse-dot {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.5);
    opacity: 0.5;
  }
}

.status-indicator {
  animation: pulse-dot 2s infinite;
}

/* Smooth transitions for all interactive elements */
button, a, input, textarea, .interactive{
  transition-property: all;
  transition-duration: 200ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

/* Focus states */
button:focus-visible, a:focus-visible, input:focus-visible{
  outline: 2px solid transparent;
  outline-offset: 2px;
  --tw-ring-offset-shadow: var(--tw-ring-inset) 0 0 0 var(--tw-ring-offset-width) var(--tw-ring-offset-color);
  --tw-ring-shadow: var(--tw-ring-inset) 0 0 0 calc(2px + var(--tw-ring-offset-width)) var(--tw-ring-color);
  box-shadow: var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow, 0 0 #0000);
  --tw-ring-opacity: 1;
  --tw-ring-color: rgb(255 112 51 / var(--tw-ring-opacity, 1));
  --tw-ring-offset-width: 2px;
  --tw-ring-offset-color: #111827;
}

/* Loading skeleton animation */
@keyframes shimmer {
  0% {
    background-position: -1000px 0;
  }
  100% {
    background-position: 1000px 0;
  }
}

.skeleton {
  background: linear-gradient(
    90deg,
    #374151 0%,
    #4b5563 50%,
    #374151 100%
  );
  background-size: 1000px 100%;
  animation: shimmer 2s infinite;
}

/* Scroll-to-bottom button styles */
.scroll-to-bottom-inside{
  position: absolute;
  bottom: 9rem;
  right: 1rem;
  z-index: 30;
  height: 3rem;
  width: 3rem;
  border-radius: 9999px;
  --tw-bg-opacity: 1;
  background-color: rgb(245 88 24 / var(--tw-bg-opacity, 1));
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity, 1));
  --tw-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 10px 15px -3px var(--tw-shadow-color), 0 4px 6px -4px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}
.scroll-to-bottom-inside:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(224 74 15 / var(--tw-bg-opacity, 1));
}
.scroll-to-bottom-inside{
  display: flex;
  align-items: center;
  justify-content: center;
  transition-property: all;
  transition-duration: 300ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.scroll-to-bottom-inside:hover{
  --tw-scale-x: 1.1;
  --tw-scale-y: 1.1;
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
  --tw-shadow: 0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 20px 25px -5px var(--tw-shadow-color), 0 8px 10px -6px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  box-shadow: 0 8px 25px rgba(229, 95, 42, 0.4);
}

/* Mobile-only styles */
@media (max-width: 768px) {
  .hidden-mobile {
    display: none !important;
  }

  #root {
    padding: 0 !important;
    height: 100vh !important;
    overflow: hidden !important;
  }

  .chat-input {
    font-size: 16px !important;
  }

  .full-chat {
    height: 100vh !important;
  }

  body {
    height: 100vh !important;
    overflow: hidden !important;
    -webkit-overflow-scrolling: touch !important;
  }

  .chat-scrollbar {
    -webkit-overflow-scrolling: touch;
    scroll-snap-type: none;
    overscroll-behavior: contain;
  }

  .scroll-to-bottom-inside{
    bottom: 7rem;
    right: 0.75rem;
    height: 2.5rem;
    width: 2.5rem;
  }
}

/* Widget Mode Styles */
.widget-mode {
  width: 100%;
  height: 100%;
  max-width: 384px;
  max-height: 500px;
  overflow: hidden;
  position: relative;
}

.widget-wrapper {
  width: 100%;
  height: 100vh;
  max-height: 500px;
  background: transparent;
  display: flex;
  flex-direction: column;
}

.widget-chat {
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.full-chat {
  height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}

/* Full Page Mode Styles */
.fullpage-mode {
  min-height: 100vh;
  width: 100%;
}

/* Responsive widget mode for mobile */
@media (max-width: 480px) {
  .widget-mode {
    max-width: 100vw;
    max-height: 100vh;
    width: 100vw;
    height: 100vh;
  }

  .widget-mode body {
    height: 100vh !important;
    overflow: hidden !important;
  }

  .widget-mode .message-bubble {
    max-width: 95%;
    font-size: 14px;
  }

  .widget-mode .chat-input {
    font-size: 16px !important;
  }
}
